@charset "UTF-8";
/* Reset Style */
html {
  overflow-y: scroll; }

body {
  line-height: 1;
  color: #000; }

/* html5doctor.com Reset Stylesheet v1.6.1
	Last Updated: 2010-09-17 Author: Richard Clark - http://richclarkdesign.com Twitter: @rich_clark */
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent; }

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

nav ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent; }

ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none; }

mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold; }

del {
  text-decoration: line-through; }

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0; }

input, select {
  vertical-align: middle; }

/* YUI 3.4.1 (build 4118) Copyright 2011 Yahoo! Inc. All rights reserved.
Licensed under the BSD License. http://yuilibrary.com/license/ */
body {
  font: 13px/1.231 arial,helvetica,clean,sans-serif;
  *font-size: small;
  *font: x-small; }

select, input, button, textarea {
  font: 99% arial,helvetica,clean,sans-serif; }

table {
  font-size: inherit;
  font: 100%; }

pre, code, kbd, samp, tt {
  font-family: monospace;
  *font-size: 108%;
  line-height: 100%; }

/* 汎用クラス */
[class^="mark"] {
  padding-left: 15px;
  background: no-repeat left .5em; }

.mark1 {
  background-image: url(../img/share/mark_1.gif); }

.mark2 {
  background-image: url(../img/share/mark_2.gif); }

.mark3 {
  background-image: url(../img/share/mark_3.gif); }

.mark4 {
  background-image: url(../img/share/mark_4.gif); }

.mark5 {
  background-image: url(../img/share/mark_5.gif); }

.opacity {
  opacity: 0.55;
  -moz-opacity: 0.55;
  filter: alpha(opacity=55);
  -ms-filter: "alpha(opacity=55)"; }

.inline_block {
  display: inline-block;
  *display: inline;
  *zoom: 1; }

.table_cell {
  display: table-cell;
  *display: inline;
  *zoom: 1;
  vertical-align: middle; }

.fr,
.imgR {
  float: right;
  margin-left: 10px; }

.fl,
.imgL {
  float: left;
  margin-right: 10px; }

.imgC,
.tC,
.btn {
  text-align: center; }

.imgC {
  display: block;
  margin-left: auto;
  margin-right: auto; }

.tR,
.sign {
  text-align: right; }

.box_size {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

.ts_none {
  -webkit-text-shadow: none !important;
  -moz-text-shadow: none !important;
  -ms-text-shadow: none !important;
  -o-text-shadow: none !important;
  text-shadow: none !important; }

.bs_none {
  -webkit-box-shadow: none !important;
  -moz-box-shadow: none !important;
  -ms-box-shadow: none !important;
  -o-box-shadow: none !important;
  box-shadow: none !important; }

/* clearfix */
.clearfix, #header #nav, #footer {
  *zoom: 1; }
  .clearfix:after, #header #nav:after, #footer:after {
    content: ".";
    display: block;
    clear: both;
    height: 0;
    visibility: hidden; }

/*----------------------------------------------------
	element style
----------------------------------------------------*/
body {
  background: #f5f5f5;
  color: #333;
  font-family: "游ゴシック", "メイリオ", "Meiryo", verdana, "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", Osaka, "ＭＳ Ｐゴシック", "MS PGothic", Sans-Serif;
  overflow: hidden; }

input, textarea {
  font-family: "游ゴシック", "メイリオ", "Meiryo", verdana, "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", Osaka, "ＭＳ Ｐゴシック", "MS PGothic", Sans-Serif; }

* html body, * html input, * html textarea {
  font-family: "ＭＳ Ｐゴシック", "MS PGothic", Sans-Serif; }

.externalIcon {
  margin: 0 3px;
  vertical-align: middle; }

a:link {
  color: #06f; }

a:visited {
  color: #39f; }

a:hover {
  text-decoration: none;
  color: #00f; }

a:active {
  color: #00f; }

/*----------------------------------------------------
	ヘッダー
----------------------------------------------------*/
#header {
  width: 100%;
  /*margin-bottom: $base_margin_bottom;*/
  height: 23%;
  overflow: hidden;
  position: fixed;
  z-index: 1; }
  #header #nav li {
    float: left; }

#header:before {
  background: #ff9400;
  content: "";
  width: 120%;
  height: 23%;
  -webkit-transform: rotate(-3deg);
  -moz-transform: rotate(-3deg);
  -ms-transform: rotate(-3deg);
  -o-transform: rotate(-3deg);
  transform: rotate(-3deg);
  position: fixed;
  top: -13%;
  left: -10%;
  z-index: -1; }

/*----------------------------------------------------
	フッター
----------------------------------------------------*/
#footer {
  bottom: 0;
  width: 100%;
  height: 23%;
  overflow: hidden;
  position: fixed;
  z-index: 1; }
  #footer .copyright {
    text-align: center; }

#footer:before {
  background: #404040;
  content: "";
  width: 120%;
  height: 23%;
  -webkit-transform: rotate(177deg);
  -moz-transform: rotate(177deg);
  -ms-transform: rotate(177deg);
  -o-transform: rotate(177deg);
  transform: rotate(177deg);
  position: fixed;
  bottom: -13%;
  left: -10%;
  z-index: -1; }

/*----------------------------------------------------
	#main
----------------------------------------------------*/
#main {
  width: 1200px;
  background: #f5f5f5;
  margin: auto; }
  #main table, #main ol, #main ul, #main dl, #main blockquote, #main pre, #main p {
    margin-bottom: 15px; }

/*----------------------------------------------------
	印刷用スタイル
----------------------------------------------------*/
@media print {
  * html body {
    zoom: .7; } }
